class Navbar extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            navbarPlaceholderHeight: 100,
            sidebarOpen: false
        };

        this.onSetSidebarOpen = this.onSetSidebarOpen.bind(this);
        this.menuOpen = this.menuOpen.bind(this);
        this.onSetSidebarClose = this.onSetSidebarClose.bind(this);
    }

    onSetSidebarOpen(open) {
        this.setState({ sidebarOpen: open });
    }

    onSetSidebarClose(event) {
        event.preventDefault();
        this.onSetSidebarOpen(false);
        
    }

    menuOpen(event) {
        event.preventDefault();
        this.onSetSidebarOpen(true);
    }

    componentDidMount() {
        
        
            this.changeNavbarPlaceholderHeight();
            this.changeNavbarHeight();

        

        // let logo = this.nav.querySelector(".logo"),
        //     _this = this;

        // logo.addEventListener("load", function() {
        //     _this.changeNavbarPlaceholderHeight();
        // });

    }

    changeNavbarHeight() {
        /* While the name states changeNavbarHeight, this does not directly change the navbar height. It simply reduces the width of the logo, which reduces the height and thereby the overall navbar height.

		Also this slightly reduces the vertical padding

		*/

        window.addEventListener("scroll", function() {
            if (this.scrollY > 0) {
                document.querySelector("nav").classList.add("scrolled");
            } else {
                document.querySelector("nav").classList.remove("scrolled");
            }
        });
    }

    changeNavbarPlaceholderHeight() {
        let navBar = document.querySelector("nav");
        let navbarPlaceholderHeight = navBar.offsetHeight;
        this.setState({
            navbarPlaceholderHeight: navbarPlaceholderHeight
        });
    }

    render() {
        const placeholder = this.props.placeholder;
        return (
            <React.Fragment>
                <Sidebar
                    sidebar={(
                        <div className="sidebar-contents">
            <div className="logo">
                
                <h5 className="nav-name text-secondary" onClick={() => scroll.scrollToTop()}>Mit Suthar</h5>
                
            </div>
            <div className="links text-secondary">
                {/* <NavLinks /> */}
                <ul className="navbar-links">
                   <Scrollspy
                    items={['home', 'about', 'project']}      
                    currentClassName="side-active"  
                    offset={-200}

                >

                    <li onClick={this.onSetSidebarClose}>
                        <Scroll type="id" element="home">
                        <a href="#" > <span>Home</span></a>
                        </Scroll>
                    </li>

                    <li onClick={this.onSetSidebarClose}>
                        <Scroll type="id" element="about">
                        <a href="#"><span>About</span></a>
                        </Scroll>
                    </li>
                    <li onClick={this.onSetSidebarClose}>
                        <Scroll type="id" element="project">
                        <a href="#"><span>Project</span></a>
                        </Scroll>
                    </li>
                    <ThemeSwitchButton
                key="themeswitcher"
                darkmode={true}
                 />
                    

                </Scrollspy>
                
                   </ul>
            </div>
            <div className="social-links">
                <SocialLinks />
            </div>
        </div>
                    )}




                    open={this.state.sidebarOpen}
                    onSetOpen={this.onSetSidebarOpen}
                    sidebarClassName="sidebar-content"
                    styles={{
                        sidebar: {
                            zIndex: 101,
                            position: "fixed"
                        },
                        overlay: {
                            zIndex: 100
                        },
                        dragHandle: {
                            position: "fixed",
                            zIndex: "99999"
                        }
                    }}
                >
                    <span></span>
                </Sidebar>
                <nav className="text-secondary" ref={c => (this.nav = c)}>
                    <a href="#mobilenav" id="menu-open" onClick={this.menuOpen}>
                        <span className="icon">
                            <Hamburger />
                        </span>
                    </a>
                   <div>
                       
                       <h5 className="nav-name" onClick={() => scroll.scrollToTop()} style={{cursor:"pointer"}}>Mit Suthar</h5>

                       

                    

                   </div>
                    {/* <NavLinks/> */}
                   <ul className="navbar-links">
                   <Scrollspy
                    items={['home', 'about', 'project']}      
                    currentClassName="is-active"  
                    offset={-200}
                >

                    <li>
                        <Scroll type="id" element="home">
                        <a href="#"> <span>Home</span></a>
                        </Scroll>
                    </li>

                    <li>
                        <Scroll type="id" element="about">
                        <a href="#"><span>About</span></a>
                        </Scroll>
                    </li>
                    <li>
                        <Scroll type="id" element="project">
                        <a href="#"><span>Project</span></a>
                        </Scroll>
                    </li>
                    <ThemeSwitchButton
                key="themeswitcher"
                darkmode={true}
                 />
                    

                </Scrollspy>
                   </ul>
                </nav>
                {placeholder && (
                    <div
                        className="navbar-placeholder"
                        style={{
                            height: this.state.navbarPlaceholderHeight + "px"
                        }}
                    ></div>
                )}
            </React.Fragment>
        );
    }
}

export default Navbar;
